/* global logoMenuData, wpaasHelpDocs */
/**
 * External dependencies
 */
import classnames from 'classnames';

/**
 * WordPress dependencies
 */
import {
	Button,
	Popover,
	Icon,
	MenuGroup,
	MenuItem,
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { SVG, Path } from '@wordpress/primitives';
import { registerPlugin } from '@wordpress/plugins';
import { useState, useEffect } from '@wordpress/element';
import { compose } from '@wordpress/compose';
import { useDispatch, useSelect, withDispatch, withSelect } from '@wordpress/data';
import { ComplementaryArea } from '@wordpress/interface';
import {
	chevronDown,
	cog,
	help,
	reusableBlock,
	comment,
	plugins,
	wordpress,
} from '@wordpress/icons';

/**
 * Internal Dependencies
 */
import { MainDashboardButton } from '@wpex/nextgen-edit-site';
import {
	__experimentalMainDashboardButton as MainEditPostDashboardButton,
} from '@wordpress/edit-post';
import { constant, NextgenTooltip } from '@wpex/nextgen-common';
import './style.scss';

const { EID_PREFIX } = constant;

export const LogoMenu = ( props ) => {
	const [ isVisible, setVisible ] = useState( false );

	const toggleVisible = () => setVisible( ! isVisible );

	const gritter = document.getElementsByClassName( 'gritter-item' );

	/* istanbul ignore next */
	const { isMobile } = useSelect( ( select ) => ( {
		isMobile: select( 'core/viewport' ).isViewportMatch( '< medium' ),
	} ), [] );

	const { enableComplementaryArea } = useDispatch( 'core/interface' );

	const openLogoMenuComplementaryArea = () => enableComplementaryArea( 'nextgen/edit-site', 'edit-site/logo-menu' );

	/* istanbul ignore next */
	useEffect( () => {
		if ( !! gritter.length ) {
			createNotice( 'success', __( 'Cache cleared', 'nextgen' ), { type: 'snackbar' } );
		}
	}, [ gritter ] );

	const { createNotice } = props;

	const DashboardButton = window.nextgen?.editSite ? MainDashboardButton : MainEditPostDashboardButton;

	return (
		<DashboardButton>
			<div className="logo-menu__logo-wrapper">
				<Button
					className="logo-menu__logo"
					onClick={ isMobile ? openLogoMenuComplementaryArea : toggleVisible }
					icon={ (
						<SVG width="27" height="24" viewBox="0 0 27 24" fill="none" xmlns="http://www.w3.org/2000/svg">
							<Path d="M23.2882 1.09799C20.4824 -0.6532 16.7887 -0.237394 13.4989 1.83136C10.2102 -0.236251 6.51642 -0.652058 3.71177 1.09799C-0.721195 3.86242 -1.26015 10.986 2.50913 17.0072C5.2886 21.4462 9.63231 24.0473 13.5 23.9993C17.3677 24.0473 21.7125 21.4474 24.4909 17.0072C28.2602 10.986 27.7212 3.86242 23.2882 1.09799ZM4.54596 15.7358C3.75297 14.4689 3.16824 13.1313 2.80664 11.7593C2.46679 10.4673 2.33977 9.20622 2.43017 8.01135C2.59838 5.78724 3.5058 4.05433 4.98422 3.13133C6.46378 2.20833 8.42166 2.15464 10.4974 2.97826C10.8098 3.10277 11.1187 3.2467 11.4254 3.40549C10.3189 4.40616 9.30161 5.61018 8.43653 6.99125C6.14567 10.6501 5.44994 14.7145 6.24751 17.9622C5.62273 17.2905 5.05288 16.5445 4.54596 15.7358ZM24.1922 11.7593C23.8306 13.1313 23.2459 14.4689 22.4529 15.7358C21.946 16.5445 21.3761 17.2893 20.7502 17.9633C21.4642 15.0561 20.9813 11.4943 19.2283 8.15528C19.1047 7.91996 18.8198 7.84228 18.5955 7.98279L13.1373 11.3881C12.9279 11.5183 12.8649 11.7936 12.9954 12.0015L13.7952 13.2798C13.9257 13.4888 14.2014 13.5516 14.4097 13.4214L17.9478 11.2144C18.0668 11.5537 18.1721 11.8953 18.2637 12.2391C18.6035 13.5311 18.7305 14.7922 18.6401 15.9871C18.4719 18.2112 17.5645 19.9441 16.0861 20.8671C15.3469 21.3274 14.4887 21.5719 13.5561 21.5982C13.5366 21.5982 13.516 21.5982 13.4966 21.5982C13.4783 21.5982 13.46 21.5982 13.4428 21.5982C12.5102 21.5719 11.652 21.3274 10.9128 20.8671C9.43321 19.9441 8.52693 18.2112 8.35872 15.9871C8.26832 14.7922 8.39534 13.5311 8.73519 12.2391C9.09678 10.8672 9.68152 9.5295 10.4745 8.26266C11.2675 6.99582 12.215 5.88433 13.2917 4.95905C14.3056 4.08745 15.3858 3.42034 16.5015 2.97826C18.5783 2.1535 20.5362 2.20833 22.0146 3.13133C23.4942 4.05433 24.4005 5.78724 24.5687 8.01135C24.6602 9.20622 24.5332 10.4673 24.1922 11.7593Z" fill="#111111" />
						</SVG>
					) }
				>
					<span className="logo-menu__logo__text">
						{ __( 'Site Editor', 'nextgen' ) }
					</span>
					<Icon
						icon={ chevronDown }
						className={ classnames(
							'chevron', {
								'is-open': isVisible,
							}
						) }
					/>
				</Button>

				{ ( isVisible && ! isMobile ) && (
					<Popover
						className="logo-menu__popover"
						focusOnMount="container"
						onClose={ () => setVisible( false ) }
						noArrow="false"
					>
						<LogoMenuContent
							props={ props }
							onToggleVisible={ toggleVisible }
						/>
					</Popover>
				) }

				{ isMobile && (
					<ComplementaryArea
						scope={ window.nextgen?.editSite ? 'nextgen/edit-site' : 'core/edit-post' }
						identifier="edit-site/logo-menu"
						smallScreenTitle={ __( 'Main menu', 'nextgen' ) }
						className="edit-site-mobile-nav__complementary"
						isPinnable={ false }>
						<LogoMenuContent
							props={ props }
							onToggleVisible={ toggleVisible }
						/>
					</ComplementaryArea>
				) }
			</div>
		</DashboardButton>
	);
};

export const LogoMenuContent = ( props ) => {
	const { adminUrl, overview, settings, flush, toggleFeedbackModal, toggleEditorPreferenceModal } = props.props;
	const { onToggleVisible } = props;

	const helpDocsEnabled = typeof wpaasHelpDocs !== 'undefined';

	return (
		<>
			{ adminUrl &&
			<MenuGroup label={ __( 'WordPress Management', 'nextgen' ) }>
				<MenuItem
					href={ adminUrl + 'plugins.php' }
					data-eid={ `${ EID_PREFIX }.logo_menu/manage.plugins.click` }
					icon={ plugins }>
					{ __( 'Manage Plugins', 'nextgen' ) }
				</MenuItem>
				<MenuItem
					href={ adminUrl + 'nav-menus.php' }
					data-eid={ `${ EID_PREFIX }.logo_menu/manage.nav_menus.click` }
					icon={ <SVG width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><Path d="m21.32691,4.00962l-4.84877,0l-1.61626,0l-11.3138,0l0,16.16257l12.93006,0l0,-9.69754l4.84877,0l0,-6.46503zm-16.16257,1.61626l9.69754,0l0,3.23251l-9.69754,0l0,-3.23251zm9.69754,12.93006l-9.69754,0l0,-8.08129l9.69754,0l0,8.08129zm4.84877,-9.69754l-3.23251,0l0,-3.23251l3.23251,0l0,3.23251zm-2.42439,-1.61626c0,-0.44447 0.36366,-0.80813 0.80813,-0.80813c0.44447,0 0.80813,0.36366 0.80813,0.80813c0,0.44447 -0.36366,0.80813 -0.80813,0.80813c-0.44447,0 -0.80813,-0.36366 -0.80813,-0.80813zm-4.04064,6.46503l-6.46503,0l0,-1.61626l6.46503,0l0,1.61626zm0,3.23251l-6.46503,0l0,-1.61626l6.46503,0l0,1.61626z" /></SVG> }>
					{ __( 'Manage Site Menus', 'nextgen' ) }
				</MenuItem>
				<MenuItem
					href={ `${ adminUrl }customize.php?return=${ encodeURIComponent( window.location.pathname + window.location.search ) }` }
					data-eid={ `${ EID_PREFIX }.logo_menu/manage.customize.click` }
					icon={ <SVG width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><Path d="m20.49041,12.60577l0,-6.34233l-2.37837,0l0,-2.37837l-13.47745,0l0,6.34233l13.47745,0l0,-2.37837l0.79279,0l0,3.17116l-7.92791,0l0,2.37837l-1.58558,0l0,7.13512l4.75675,0l0,-7.13512l-1.58558,0l0,-0.79279l7.92791,0zm-3.96396,-3.96396l-10.30628,0l0,-3.17116l10.30628,0l0,3.17116zm-3.96396,10.30628l-1.58558,0l0,-3.96396l1.58558,0l0,3.96396z" /></SVG> }>
					{ __( 'Customize Site Design', 'nextgen' ) }
				</MenuItem>
				<MenuItem
					href={ adminUrl + 'users.php' }
					data-eid={ `${ EID_PREFIX }.logo_menu/manage.users.click` }
					icon={ <SVG width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><Path d="m12.51299,16.5284c1.2328,0.8388 2.56642,1.51899 3.96934,2.02436l-0.60334,1.46072c-1.67339,-0.57984 -3.24176,-1.42706 -4.64413,-2.50862c-0.2642,-0.23268 -0.42424,-0.5615 -0.44457,-0.91295c0.02715,-0.47243 0.24491,-0.91358 0.60334,-1.22256c0.5954,-0.61922 1.49247,-1.5401 1.49247,-4.08048c0.03175,-3.4692 -1.62743,-3.91377 -3.08021,-3.91377c-1.45278,0 -3.11196,0.44457 -3.11196,3.91377c0,2.54038 0.89707,3.46127 1.49247,4.08048c0.37919,0.29889 0.61772,0.74147 0.65891,1.22256c-0.02028,0.35145 -0.18037,0.68027 -0.44457,0.91295c-1.40238,1.08157 -2.97074,1.92878 -4.64413,2.50862l-0.60334,-1.46072c1.40099,-0.50641 2.73417,-1.18366 3.96934,-2.01643l-0.05557,-0.06351c-0.67479,-0.6986 -1.93704,-2.00849 -1.93704,-5.18396c-0.02382,-4.54886 2.53244,-5.50151 4.67588,-5.50151c2.14344,0 4.6997,0.95264 4.6997,5.50151c0,3.17547 -1.26225,4.48536 -1.93704,5.18396l-0.05557,0.05557zm4.76321,-1.58774l0.05557,-0.05557c0.67479,-0.6986 1.93704,-2.00849 1.93704,-5.18396c0,-4.54886 -2.55626,-5.50151 -4.6997,-5.50151c-0.30278,0.00028 -0.60517,0.0215 -0.90501,0.06351c-0.45306,0.06604 -0.89421,0.19705 -1.30988,0.389c0.6909,0.24628 1.3233,0.63278 1.85765,1.13523c0.11908,0 0.23022,0 0.35724,0c1.45278,0 3.11196,0.44457 3.11196,3.91377c0,2.54038 -0.89707,3.46127 -1.49247,4.08048c-0.37915,0.29889 -0.61771,0.74147 -0.65891,1.22256c0.02032,0.35145 0.18037,0.68027 0.44457,0.91295c1.40237,1.08157 2.97073,1.92878 4.64413,2.50862l0.60334,-1.46072c-1.39459,-0.5072 -2.72011,-1.18731 -3.94553,-2.02436z" /></SVG> }>
					{ __( 'Manage Users', 'nextgen' ) }
				</MenuItem>
				<MenuItem
					href={ adminUrl + 'edit-comments.php' }
					data-eid={ `${ EID_PREFIX }.logo_menu/manage.edit_comments.click` }
					icon={ <SVG width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><Path d="m4.20828,4.18045l0,12.26674l10.48807,0l4.84536,3.39635l0,-15.6631l-15.33343,0zm13.80009,12.71908l-2.82135,-1.98568l-9.44539,0l0,-9.20006l12.26674,0l0,11.18574zm-9.96673,-6.58571c0,-0.42342 0.34325,-0.76667 0.76667,-0.76667c0.42342,0 0.76667,0.34325 0.76667,0.76667c0,0.42343 -0.34325,0.76667 -0.76667,0.76667c-0.42342,0 -0.76667,-0.34324 -0.76667,-0.76667zm3.06669,0c0,-0.42342 0.34324,-0.76667 0.76667,-0.76667c0.42343,0 0.76667,0.34325 0.76667,0.76667c0,0.42343 -0.34324,0.76667 -0.76667,0.76667c-0.42343,0 -0.76667,-0.34324 -0.76667,-0.76667zm3.06669,0c0,-0.42342 0.34324,-0.76667 0.76667,-0.76667c0.42343,0 0.76667,0.34325 0.76667,0.76667c0,0.42343 -0.34324,0.76667 -0.76667,0.76667c-0.42343,0 -0.76667,-0.34324 -0.76667,-0.76667z" /></SVG> }>
					{ __( 'Manage Comments', 'nextgen' ) }
				</MenuItem>
				<MenuItem
					href={ adminUrl }
					data-eid={ `${ EID_PREFIX }.logo_menu/manage.wpadmin.click` }
					icon={ wordpress }
					className="wordpress">
					{ __( 'WordPress Admin', 'nextgen' ) }
				</MenuItem>
			</MenuGroup>
			}
			{
				( overview || settings ) &&
				<MenuGroup label={ __( 'Hosting', 'nextgen' ) } >
					{ overview &&
					<MenuItem
						href={ overview }
						data-eid={ `${ EID_PREFIX }.logo_menu/hosting.overview.click` }
						icon={ <SVG width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg"><path id="svg_29" d="m19.00004,6.90274l0,1.55556l-10.88895,0l0,-1.55556l10.88895,0zm-10.88895,6.22226l10.88895,0l0,-1.55556l-10.88895,0l0,1.55556zm0,4.66669l10.88895,0l0,-1.55556l-10.88895,0l0,1.55556zm-2.33335,-10.88895c-0.38889,0 -0.77778,0.31111 -0.77778,0.77778c0,0.46667 0.38889,0.77778 0.77778,0.77778c0.38889,0 0.77778,-0.31111 0.77778,-0.77778c0,-0.46667 -0.31111,-0.77778 -0.77778,-0.77778zm0,4.66669c-0.38889,0 -0.77778,0.31111 -0.77778,0.77778c0,0.46667 0.38889,0.77778 0.77778,0.77778c0.38889,0 0.77778,-0.31111 0.77778,-0.77778c0,-0.46667 -0.31111,-0.77778 -0.77778,-0.77778zm0,4.66669c-0.38889,0 -0.77778,0.38889 -0.77778,0.77778c0,0.38889 0.38889,0.77778 0.77778,0.77778c0.38889,0 0.77778,-0.38889 0.77778,-0.77778c0,-0.38889 -0.31111,-0.77778 -0.77778,-0.77778z" /></SVG> }>
						{ __( 'Hosting Overview', 'nextgen' ) }
					</MenuItem>
					}
					{ settings &&
					<MenuItem
						href={ settings }
						data-eid={ `${ EID_PREFIX }.logo_menu/hosting.settings.click` }
						icon={ cog }>
						{ __( 'Hosting Settings', 'nextgen' ) }
					</MenuItem>
					}
				</MenuGroup>
			}

			<MenuGroup label={ __( 'Tools', 'nextgen' ) } >
				<MenuItem
					data-tip
					data-for="editorPreferenceTooltip"
					onClick={ () => {
						onToggleVisible();
						toggleEditorPreferenceModal();
					} }
					icon={ <SVG width="23" height="22" viewBox="0 0 23 22" fill="none" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" clipRule="evenodd" d="M-0.5 0V20H21.5V0H-0.5ZM19.5 2V6H1.5V2H19.5ZM1.5 18V8H19.5V18H1.5ZM2.5 4C2.5 3.45 2.95 3 3.5 3C4.05 3 4.5 3.45 4.5 4C4.5 4.55 4.05 5 3.5 5C2.95 5 2.5 4.55 2.5 4ZM5.5 4C5.5 3.45 5.95 3 6.5 3C7.05 3 7.5 3.45 7.5 4C7.5 4.55 7.05 5 6.5 5C5.95 5 5.5 4.55 5.5 4ZM8.5 4C8.5 3.45 8.95 3 9.5 3C10.05 3 10.5 3.45 10.5 4C10.5 4.55 10.05 5 9.5 5C8.95 5 8.5 4.55 8.5 4Z" fill="#111111" /></SVG> }>
					{ __( 'Editor Preference', 'nextgen' ) }
					<NextgenTooltip
						id="editorPreferenceTooltip"
						type="info"
						title={ __( 'Is this editor right for you?', 'nextgen' ) }
						content={ __( 'Learn about other WordPress site building options here.', 'nextgen' ) } />
				</MenuItem>
				{ flush &&
				<MenuItem
					href={ flush }
					data-eid={ `${ EID_PREFIX }.logo_menu/tools.flush_cache.click` }
					icon={ <SVG fill="none" width="20" height="20" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><Path clipRule="evenodd" d="m3.00998 8.44755h1.55335 3.43667v-1.55336h-2.45029c1.36066-1.93908 3.82023-2.77396 6.08009-2.06385 2.2599.7101 3.8 2.80172 3.807 5.17056h1.5533c-.0088-2.95846-1.8791-5.59098-4.6698-6.57297-2.79076-.98199-5.89747-.10076-7.75697 2.20028l-.00003-2.62821h-1.55335zm8.92752 4.66005v-1.5534h5.0526v5.4146h-1.5533v-2.572c-1.8622 2.3044-4.9748 3.1845-7.76795 2.1964-2.79311-.9881-4.65994-3.6296-4.6589-6.5923h1.55335c.00704 2.3688 1.54704 4.4604 3.80695 5.1705 2.25995.7101 4.71945-.1248 6.08015-2.0638z" fill="currentColor" fillRule="evenodd" /></SVG> }>
					{ __( 'Flush Cache', 'nextgen' ) }
				</MenuItem>
				}
				<MenuItem
					id="mwp-view-connection-key"
					data-eid={ `${ EID_PREFIX }.logo_menu/tools.connection_management.click` }
					icon={ reusableBlock }>
					{ __( 'Connection Management', 'nextgen' ) }
				</MenuItem>
			</MenuGroup>

			<MenuGroup label={ __( 'Support', 'nextgen' ) }>
				<MenuItem
					onClick={ () => {
						onToggleVisible();
						toggleFeedbackModal();
					} }
					data-eid={ `${ EID_PREFIX }.logo_menu/support.feedback.click` }
					data-test="nextgen-feedback-button"
					icon={ comment }>
					{ __( 'Give Feedback', 'nextgen' ) }
				</MenuItem>

				{ helpDocsEnabled &&
				<MenuItem
					onClick={ onToggleVisible }
					data-eid={ `${ EID_PREFIX }.logo_menu/support.help.click` }
					icon={ help }
					id="wp-admin-bar-wpaas-help-and-support">
					{ __( 'Help & Support', 'nextgen' ) }
				</MenuItem>
				}
			</MenuGroup>
		</>
	);
};

/* istanbul ignore next */
registerPlugin( 'logo-menu', {
	render: compose( [

		withSelect( () => {
			return {
				adminUrl: logoMenuData?.admin || '',
				overview: logoMenuData?.overview || '',
				settings: logoMenuData?.settings || '',
				flush: logoMenuData?.flush || '',
			};
		} ),

		withDispatch( ( dispatch ) => {
			const { createNotice } = dispatch( 'core/notices' );
			const { toggleFeedbackModal } = dispatch( 'nextgen/feedback-modal' );
			const { toggleEditorPreferenceModal } = dispatch( 'nextgen/editor-preference-modal' );

			return {
				createNotice,
				toggleFeedbackModal,
				toggleEditorPreferenceModal,
			};
		} ),

	] )( LogoMenu ),
} );
